<template>
  <div class="contentHtml">
  	<ul class="listBox">
  		<template v-for="listData in listDatas">
	  		<router-link :to="'/information/'+listData.id">
		    		<li>
		    			<div class="leftBox">
		    				<div class="listTitle">{{listData.title.rendered}}</div>
		    				<div class="listInfo"><span class="but1">置顶</span><span>新媒之家 {{listData.date}} 阅读：{{listData.id}}</span></div>
		    			</div>
		    			<div class="rightBox"><img src="../assets/img.png"></div>
		    		</li>
	    		</router-link>
    		</template>
  		<!--<router-link to="/test">
	    		<li>
	    			<div class="leftBox">
	    				<div class="listTitle">外交部：中方救援“桑吉”号不力的说法不符事实，不负责任</div>
	    				<div class="listInfo"><span class="but1">置顶</span><span>新媒之家 09－12 阅读：13773</span></div>
	    			</div>
	    			<div class="rightBox"><img src="../assets/img.png"></div>
	    		</li>
    		</router-link>-->
   </ul>
  </div>
</template>

<script>
 $("title").text("资讯")
export default {
  name: 'contentHtml',
  data () {
    return {
    	listDatas:'',
    	msg: '',
    	input:'',
    	message:''
    }
  },
  created:function(){
  	var _self=this;
  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{},function(data){
  		console.log(eval(data))
		_self.listDatas = eval(data);
	})
  }
  ,
  methods: {
    change: function (event) {
    		this.msg = $("p").text()
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



