<template>
  <div class="indexHtml">
  	<ul class="listBox">
  		<template v-for="listData in listDatas">
	  		<router-link :to="'/test/'+listData.id">
		    		<li>
		    			<div class="leftBox">
		    				<div class="listTitle">{{listData.title.rendered}}</div>
		    				<div class="listInfo"><span class="but1">置顶</span><span>新媒之家 {{listData.date}} 阅读：{{listData.id}}</span></div>
		    			</div>
		    			<div class="rightBox"><img src="../assets/img.png"></div>
		    		</li>
	    		</router-link>
    		</template>
   </ul>
   <router-link to="/information">去资讯列表</router-link>
  </div>
</template>

<script>

export default {
  name: 'indexHtml',
  data () {
    return {
    	listDatas:'',
    	msg: '',
    	input:'',
    	message:''
    }
  },
  created:function(){
  	var _self=this;
  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{},function(data){
  		console.log(eval(data))
		_self.listDatas = eval(data);
	})
  }
  ,
  methods: {
    change: function (event) {
    		this.msg = $("p").text()
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



