<template>
  <div class="contentHtml">
  	<div class="title">{{title}}</div>
  	<div class="idInfo">
  		<img src="../assets/img.png" width="20" height="20"/>
  		<span>作者<br/>两小时前</span>
  		<span>关注</span>
  	</div>
  	<div class="content"></div>
  </div>
</template>

<script>
 $("title").text("资讯")
export default {
  name: 'contentHtml',
  data () {
    return {
    		datas:'',
    		title:'',
    		content:''
    }
  },
  created:function(){
  	var _self=this;
  	var id = _self.$route.params.id
  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts/'+id,{},function(data){
  		console.log(data)
  		_self.datas = data;
		_self.title = data.title.rendered;
		$(".content").html(data.content.rendered);
	})
  },
  methods: {
    change: function (event) {
    		this.msg = $("p").text()
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.contentHtml{padding: 10px;}
	.contentHtml .title{font-size: 2rem;color: #333;}
	.contentHtml .content{font-size: 1rem;color: #666;}
</style>



