<template>
  <div id="app">
    <app-header></app-header>
    <app-nav></app-nav>
    <router-view/>
    <app-footer></app-footer>
  </div>
</template>
<script>
//设置字体
setFont();
$(window).resize(function(){
	setFont();
})
function setFont(){
	var winWidth = $('body').width();
	if(winWidth<769){
		$("html,body").css("font-size",20*(winWidth/640)+"px");
	}
}
export default {
  name: 'app'
}
</script>

<style>
body,html,ul,li,a,dl,dd,dt,img,a{padding: 0;margin: 0;border: 0;}
body,html{max-width: 768px;margin: 0 auto;background: #f7f7f7;}
h1, h2 ,h3,h4,h5,h6{font-weight: normal;}
ul {list-style-type: none;}
a {color: #42b983;text-decoration: none;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  font-size: 0.8rem;
}

.line{border-bottom: 1px solid #ebebeb;}

.listBox{margin: 10px 0;}
.listBox li{padding:10px;margin-bottom:10px;background: #fff;overflow: hidden;
	display: flex;display: -webkit-flex; /* Safari */justify-content:space-between;
}
.listBox li .leftBox{}
.listBox li .leftBox .listInfo{color: #999;font-size: 0.5rem;margin-top: 1.5rem;}
.listBox li .leftBox .listInfo .but1{border:1px solid #eee;margin-right: 5px;color: #ed7655; padding: 1px 2px;border-radius:5px;-webkit-border-radius:5px;}
.listBox li .rightBox img{width: 10rem;height: 7rem;}
.listBox li .listTitle{font-size: 1.5rem;color: #333;height: 4rem;}

</style>



